// eslint-disable-next-line @typescript-eslint/no-explicit-any
export default function PredictiveAnalysis({ data }: { data: any[] }) {
  const averageCompliance = data.length
    ? data.reduce((acc, curr) => acc + curr.percentage, 0) / data.length
    : 0;

  const predictedOutcome =
    averageCompliance >= 80
      ? "High likelihood that the infant will complete all vaccinations on schedule."
      : averageCompliance >= 50
      ? "Moderate likelihood that the infant will complete all vaccinations on schedule. Consider a follow-up reminder for upcoming doses."
      : "Low likelihood that the infant will complete the vaccinations on schedule. Immediate intervention is recommended.";

  return (
    <div className="bg-white rounded-lg shadow-md p-6">
      <h2 className="text-2xl font-bold mb-4">Predictive Analysis</h2>
      {data.length ? (
        <>
          <p className="mb-4">
            <span className="font-semibold">
              Average Vaccination Compliance:
            </span>{" "}
            {averageCompliance.toFixed(2)}%
          </p>
          <p>{predictedOutcome}</p>
        </>
      ) : (
        <p>No vaccination data available for prediction.</p>
      )}
    </div>
  );
}
